/*Aligns landing page logo and button*/
body { 
  background-color: #eeeee4;
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/*landing page logo*/
.logo {
  width: 750px; 
  height: auto;
  margin-bottom: -100px;
}

/*Landing Page Get started button*/
.button {
  padding: 20px 30px; 
  font-family: 'Lato', sans-serif; 
  font-weight: bold; 
  font-size: 16px; 
  letter-spacing: 1px;
  border: none;
  background-color: #3c6729; 
  color: white;
  cursor: pointer;
  border-radius: 30px;
  text-decoration: none;
}

/*back button position*/
.back {
  position: fixed;
  bottom: 20px; /* Adjust bottom position as needed */
  left: 50px; /* Adjust left position as needed */
  z-index: 999; /* Adjust z-index as needed */
}

/*next button position*/
.next {
  position: fixed;
  bottom: 20px; /* Adjust bottom position as needed */
  right: 50px; /* Adjust left position as needed */
  z-index: 999; /* Adjust z-index as needed */
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Center the container horizontally */
  margin: 10px auto; /* Margin on top and bottom, and auto margins on left and right */
}

.box {
  width: 200px;
  height: 200px;
  background-color: #b8d8be;
  margin: 10px;
  border-radius: 15%;
}